#header .header-bottom .fixed_menu .slide_menu {
  display: block;
  margin: 0 auto;
  padding: 0;
  cursor: pointer;
  outline: none;
  position: relative;
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn {
  height: 45px;
  width: 30px;
  padding: 22px 5px;
  position: relative;
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn .slide_menu-btn-two_border {
  display: block;
  width: 100%;
  border-top: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  height: 6px;
  transform: rotate(0);
  transition: transform 0.25s cubic-bezier(0.4, 0.01, 0.165, 0.99);
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module {
  position: absolute;
  background: #222222;
  top: 55px;
  right: -65px;
  padding: 15px 35px;
  display: none;
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module:after {
  content: '';
  border-color: transparent transparent #222222;
  border-style: solid;
  border-width: 12px;
  position: absolute;
  top: -18px;
  left: 0;
  width: 0px;
  right: 0;
  margin: 0 auto;
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module div {
  padding: 3px 0;
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module div a {
  color: #D3D3D3;
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn .hidden-slide_menu-module div a:hover {
  color: #419FD9;
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn.open .slide_menu-btn-two_border {
  transform: rotate(45deg) translateY(0rem);
  border-bottom: none;
}
#header .header-bottom .fixed_menu .slide_menu .slide_menu-btn.open .slide_menu-btn-two_border::after {
  position: absolute;
  top: 0px;
  content: "";
  border-bottom: 1px solid #ffffff;
  width: 100%;
  transform: rotate(-90deg) translateY(0rem);
}
