  color: #08c;
}
.items-btn.blue .item::after {
  border-color: #08c;
}
.items-btn.blue .item.active,
.items-btn.blue .item:hover {
  background: #08c;
}
.items-btn .item {
  transition: all 0.5s;
  padding: 8px 18px;
  background: #ffffff;
  cursor: pointer;
  float: left;
  position: relative;
  border-radius: 2px;
}
.items-btn .item::after {
  content: "";
  border-right: 1px solid rgba(233, 23, 151, 0.4);
  position: absolute;
  z-index: 100;
  left: 1px;
  bottom: 0;
  width: 100%;
  height: 70%;
  top: 5px;
}
.items-btn .item.active,
.items-btn .item:hover {
  background: #E91797;
  color: #ffffff;
}
.items-btn .item.active::after,
.items-btn .item:hover::after {
  border: none !important;
  display: none;
}
.items-btn .item.active a,
.items-btn .item:hover a {
  color: #ffffff;
  text-decoration: none;
}
